import React, { useState, useEffect } from "react"
import Modal from "react-modal"
import "../style/GlobalMenu.css"
import GlobalMenuHeader from "./GlobalMenuHeader"
import GlobalMenuFooter from "./GlobalMenuFooter"
import GlobalMenuItem from "./GlobalMenuItem"

const GlobalMenu = ({ isOpen, toggleMenu }) => {
  const [isClosing, setIsClosing] = useState(false)

  useEffect(() => {
    if (isOpen) {
      document.body.classList.add("menu-open") //    body menu-open  
    } else {
      document.body.classList.remove("menu-open") //    body menu-open  
    }
  }, [isOpen])

  const handleCloseModal = () => {
    setIsClosing(true) //    .
    setTimeout(() => {
      toggleMenu() //  .
      setIsClosing(false) //   .
    }, 500) // setTimeout     0.5 .
  }

  return (
    <Modal
      isOpen={isOpen}
      onRequestClose={handleCloseModal}
      contentLabel="Global Menu"
      className={`menu-modal ${isOpen && !isClosing ? "slideIn" : "slideOut"}`}
      overlayClassName="menu-overlay"
    >
      <div className="menu">
        <GlobalMenuHeader />
        <GlobalMenuItem />
        <GlobalMenuFooter />
      </div>
    </Modal>
  )
}

export default GlobalMenu
